import React from 'react';



let Profile = (props) => {
    if(props.state.login.isAuth) {
        return(
            <div>
                <div>
                    Name: {props.state.login.name}
                </div>
                <div>
                    Pass: {props.state.login.pass}
                </div>
            </div>
        )
    } else {
        return(
            <div>
                Вы не авторизированы
            </div>
        )
    }
}

export default Profile;
